import React from 'react';
import { InertiaProvider } from '@inertiajs/inertia-react';

const Wrapper = ({ children }) => (
  <InertiaProvider
    initialPage={{
      component: 'Login',
      props: {
        errors: {},
        auth: { user: null },
        flash: {},
      },
      url: '/',
      version: null,
    }}
  >
    {children}
  </InertiaProvider>
);

export default Wrapper;